
"use client";

import { useState, useEffect } from "react";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Switch } from "@/components/ui/switch";
import type { Product, ProductStatus } from "@/lib/types";

interface ProductDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  product: Product | null;
  isCreating: boolean;
  onSubmit: (productData: any) => void;
}

const categories = [
  { id: "CAT-TYRE", name: "Tyres", parent: "Fleet Spares" },
  { id: "CAT-OIL", name: "Engine Oil", parent: "Fleet Spares" },
  { id: "CAT-BATTERY", name: "Batteries", parent: "Fleet Spares" },
  { id: "CAT-ELECTRONICS", name: "Electronics", parent: "General" },
  { id: "CAT-CONSUMABLES", name: "Consumables", parent: "General" },
  { id: "CAT-SAFETY", name: "Safety Gear", parent: "Equipment" },
  { id: "CAT-TOOLS", name: "Tools", parent: "Equipment" },
  { id: "CAT-OTHER", name: "Other", parent: null },
];

const vendors = [
    { id: "VND-001", name: "Gulf Auto Parts LLC" },
    { id: "VND-002", name: "Saudi Lubricants Co." },
    { id: "VND-003", name: "Modern Fleet Solutions" },
];

const productStatuses: ProductStatus[] = ["ACTIVE", "DRAFT", "OUT_OF_STOCK", "DISCONTINUED"];

export function ProductDialog({
  open,
  onOpenChange,
  product,
  isCreating,
  onSubmit,
}: ProductDialogProps) {
  const [formData, setFormData] = useState({
    name: "",
    description: "",
    price: "",
    sku: "",
    categoryId: "CAT-OTHER",
    vendorId: "VND-001",
    unit: "Nos",
    minOrderQty: "1",
    taxPercent: "15",
    discountPercent: "0",
    status: "ACTIVE" as ProductStatus,
    stock: "0", // Represents availableQty
    reorderLevel: "10",
  });

  const [errors, setErrors] = useState<Record<string, string>>({});

  useEffect(() => {
    if (open) {
        if (product && !isCreating) {
            setFormData({
                name: product.name,
                description: product.description || "",
                price: product.price.toString(),
                sku: product.sku || "",
                categoryId: product.categoryId || "CAT-OTHER",
                vendorId: product.vendorId || "VND-001",
                unit: product.unit || "Nos",
                minOrderQty: (product.minOrderQty || 1).toString(),
                taxPercent: (product.taxPercent || 15).toString(),
                discountPercent: (product.discountPercent || 0).toString(),
                status: product.status || "ACTIVE",
                stock: (product.stock || 0).toString(),
                reorderLevel: (product.reorderLevel || 10).toString(),
            });
        } else {
            // Reset for creation
            const newSku = `SKU-${Date.now().toString().slice(-6)}`;
            setFormData({
                name: "",
                description: "",
                price: "",
                sku: newSku,
                categoryId: "CAT-OTHER",
                vendorId: "VND-001",
                unit: "Nos",
                minOrderQty: "1",
                taxPercent: "15",
                discountPercent: "0",
                status: "ACTIVE",
                stock: "0",
                reorderLevel: "10",
            });
        }
        setErrors({});
    }
  }, [product, isCreating, open]);

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.name.trim()) newErrors.name = "Name is required";
    if (!formData.sku.trim()) newErrors.sku = "SKU is required";
    if (!formData.price || parseFloat(formData.price) <= 0) newErrors.price = "Price must be a positive number";
    if (!formData.categoryId) newErrors.category = "Category is required";
    if (!formData.vendorId) newErrors.vendor = "Vendor is required";
    if (parseInt(formData.stock) < 0) newErrors.stock = "Stock cannot be negative";

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;

    onSubmit({
        name: formData.name,
        description: formData.description,
        price: parseFloat(formData.price),
        sku: formData.sku,
        categoryId: formData.categoryId,
        vendorId: formData.vendorId,
        unit: formData.unit,
        minOrderQty: parseInt(formData.minOrderQty),
        taxPercent: parseFloat(formData.taxPercent),
        discountPercent: parseFloat(formData.discountPercent),
        status: formData.status,
        stock: parseInt(formData.stock), // This should update inventory collection
        reorderLevel: parseInt(formData.reorderLevel),
    });
  };

  const handleInputChange = (
    field: string,
    value: string | number | boolean
  ) => {
    setFormData((prev) => ({ ...prev, [field]: value }));
    if (errors[field]) {
      setErrors((prev) => {
        const newErrors = { ...prev };
        delete newErrors[field];
        return newErrors;
      });
    }
  };

  const title = isCreating ? "Add New Product" : `Edit ${product?.name}`;
  const description = isCreating
    ? "Fill in the details for the new product."
    : "Make changes to your product details below.";

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-3xl">
        <form onSubmit={handleSubmit}>
          <DialogHeader>
            <DialogTitle>{title}</DialogTitle>
            <DialogDescription>{description}</DialogDescription>
          </DialogHeader>

          <div className="grid gap-4 py-4 max-h-[70vh] overflow-y-auto pr-4">
            <div className="space-y-2">
                <Label htmlFor="name">Product Name *</Label>
                <Input id="name" value={formData.name} onChange={(e) => handleInputChange("name", e.target.value)} placeholder="e.g., Tyre Michelin 295/80 R22.5" className={errors.name ? "border-destructive" : ""} />
                {errors.name && <p className="text-sm text-destructive">{errors.name}</p>}
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-2">
                  <Label htmlFor="sku">SKU *</Label>
                  <Input id="sku" value={formData.sku} onChange={(e) => handleInputChange("sku", e.target.value)} placeholder="e.g., MIC-295-80" className={errors.sku ? "border-destructive" : ""} />
                  {errors.sku && <p className="text-sm text-destructive">{errors.sku}</p>}
              </div>
              <div className="space-y-2">
                  <Label htmlFor="price">Price (SAR) *</Label>
                  <Input id="price" type="number" value={formData.price} onChange={(e) => handleInputChange("price", e.target.value)} placeholder="e.g., 1250" className={errors.price ? "border-destructive" : ""} />
                  {errors.price && <p className="text-sm text-destructive">{errors.price}</p>}
              </div>
            </div>

            <div className="space-y-2">
              <Label htmlFor="description">Description</Label>
              <Textarea id="description" value={formData.description} onChange={(e) => handleInputChange("description", e.target.value)} placeholder="e.g., Heavy-duty bus tyre" rows={3} />
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="categoryId">Category *</Label>
                <Select value={formData.categoryId} onValueChange={(value) => handleInputChange("categoryId", value)}>
                  <SelectTrigger className={errors.category ? "border-destructive" : ""}><SelectValue placeholder="Select category" /></SelectTrigger>
                  <SelectContent>
                    {categories.map((cat) => (
                      <SelectItem key={cat.id} value={cat.id}>
                        {cat.parent ? `${cat.parent} > ${cat.name}` : cat.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
                {errors.category && <p className="text-sm text-destructive">{errors.category}</p>}
              </div>
              <div className="space-y-2">
                <Label htmlFor="vendorId">Vendor *</Label>
                <Select value={formData.vendorId} onValueChange={(value) => handleInputChange("vendorId", value)}>
                  <SelectTrigger className={errors.vendor ? "border-destructive" : ""}><SelectValue placeholder="Select vendor" /></SelectTrigger>
                  <SelectContent>
                    {vendors.map((v) => (<SelectItem key={v.id} value={v.id}>{v.name}</SelectItem>))}
                  </SelectContent>
                </Select>
                {errors.vendor && <p className="text-sm text-destructive">{errors.vendor}</p>}
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                    <Label htmlFor="stock">Available Quantity</Label>
                    <Input id="stock" type="number" value={formData.stock} onChange={(e) => handleInputChange("stock", e.target.value)} placeholder="e.g., 120" className={errors.stock ? "border-destructive" : ""}/>
                     {errors.stock && <p className="text-sm text-destructive">{errors.stock}</p>}
                </div>
                <div className="space-y-2">
                    <Label htmlFor="reorderLevel">Reorder Level</Label>
                    <Input id="reorderLevel" type="number" value={formData.reorderLevel} onChange={(e) => handleInputChange("reorderLevel", e.target.value)} placeholder="e.g., 20"/>
                </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                    <Label htmlFor="unit">Unit</Label>
                    <Input id="unit" value={formData.unit} onChange={(e) => handleInputChange("unit", e.target.value)} placeholder="e.g., Nos, Kg, Box"/>
                </div>
                <div className="space-y-2">
                    <Label htmlFor="minOrderQty">Min. Order Qty</Label>
                    <Input id="minOrderQty" type="number" value={formData.minOrderQty} onChange={(e) => handleInputChange("minOrderQty", e.target.value)} placeholder="e.g., 1"/>
                </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                    <Label htmlFor="taxPercent">Tax %</Label>
                    <Input id="taxPercent" type="number" value={formData.taxPercent} onChange={(e) => handleInputChange("taxPercent", e.target.value)} placeholder="e.g., 15"/>
                </div>
                <div className="space-y-2">
                    <Label htmlFor="discountPercent">Discount %</Label>
                    <Input id="discountPercent" type="number" value={formData.discountPercent} onChange={(e) => handleInputChange("discountPercent", e.target.value)} placeholder="e.g., 5"/>
                </div>
            </div>

            <div className="space-y-2">
              <Label htmlFor="status">Status</Label>
              <Select value={formData.status} onValueChange={(value: ProductStatus) => handleInputChange("status", value)}>
                <SelectTrigger><SelectValue placeholder="Select status" /></SelectTrigger>
                <SelectContent>
                  {productStatuses.map((s) => (<SelectItem key={s} value={s}>{s}</SelectItem>))}
                </SelectContent>
              </Select>
            </div>
            
          </div>

          <DialogFooter>
            <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>
              Cancel
            </Button>
            <Button type="submit">
              {isCreating ? "Create Product" : "Save Changes"}
            </Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  );
}

    